import { useContext, useEffect, useState } from "react";
import DateContext from "../context/DateContext";
import { getClientes, getClientesActive } from "../services/clienteService";

const useClientes = (active=false) => {
  const { fecha } = useContext(DateContext);
  const [clientes, setClientes] = useState([])

  const addCliente = (objCliente) => {
    setClientes([...clientes, objCliente])
  }

  const loadClientesActive = () => {
    getClientesActive().then((rpta) => {
      if (rpta.request.status === 200) {
        setClientes(rpta.data);
      }
    });
  }

  const loadAllClientes = () => {
    console.log("all");
    getClientes().then((rpta) => {
      if (rpta.request.status === 200) {
        setClientes(rpta.data);
      }
    });
  }

  useEffect(() => {
    if(active) loadClientesActive()
    else loadAllClientes()
  }, [fecha]);

  return {
    clientes,
    addCliente,
    loadAllClientes,
    loadClientesActive,
  }
}

export default useClientes